import React, { Component } from 'react'
import { Query } from 'react-apollo'
import { gql } from 'apollo-boost'
import {
  Column,
  Title,
  Box,
  Icon,
  Control,
  Input
} from 'bloomer'
import { HouseContext } from 'contexts'
import BaseLayout from 'containers/BaseLayout'
import styled from 'styled-components'

import Module from './Module'
import { Columns } from 'bloomer/lib/grid/Columns';

const GET_HOUSE = gql`
  query ($houseId: UUID!) {
    house(houseId: $houseId) {
      id,
      rooms {
        id,
        name,
        modules {
          id,
          name,
          isActive,
          identifier,
          moduleModel {
            id, name, icon, description, color
          }
        }
      }
    }
  }
`

const SearchBar = styled.div`
  max-width: 50rem;
  margin: 0 auto 2rem;
`

export default class DashboardPage extends Component {
  state = {
    search: ''
  }

  search = (module) => {
    const { search } = this.state

    return (module.name.toLowerCase().indexOf(search) >= 0 || module.moduleModel.name.toLowerCase().indexOf(search) >= 0)
  }

  handleSearchChange = (evt) => {
    this.setState({
      search: evt.target.value.toLowerCase()
    })
  }

  _sortRooms = (rooms) => {
    return [...rooms].sort((a, b) => b.modules.length - a.modules.length)
  }

  render() {
    const { search } = this.state

    return (
      <HouseContext.Consumer>
        {({house}) => (
          <BaseLayout>
            <SearchBar>
              <Control hasIcons="right">
                <Input placeholder="Buscar" value={search} onChange={this.handleSearchChange} />
                <Icon isAlign="right" icon="search"/>
              </Control>
            </SearchBar>

            <Query query={GET_HOUSE} variables={{houseId: house.id}}>
              {({loading, error, data}) => {
                if (loading) {
                  return <p>Loading...</p>
                }

                if (error) {
                  return <p>Error...</p>
                }

                return (
                  <Columns isMultiline>
                    {this._sortRooms(data.house.rooms).map(room => (
                      <Column key={room.id} isSize="1/2">
                        <Box>
                          <Title isSize={6} style={{textTransform: 'uppercase'}}>{room.name}</Title>
                          <Columns isMultiline>
                          {room.modules.filter(this.search).map(module => (
                            <Column key={module.id} isSize={{ widescreen: '1/2' }}>
                              <Module module={module} room={room} />
                            </Column>
                          ))}
                          </Columns>
                        </Box>
                      </Column>
                    ))}
                  </Columns>
                )
              }}
            </Query>
          </BaseLayout>
        )}
      </HouseContext.Consumer>
    )
  }
}



