import React, { Component } from 'react'
import { ApolloConsumer } from 'react-apollo'
import { gql } from 'apollo-boost'

import { AUTH_TOKEN_KEY, MQTT_URI } from '../constants'
import { AuthContext, HouseContext, MqttClientContext } from 'contexts'
import mqtt from 'mqtt'

import IdentificationPage from 'pages/IdentificationPage'

const GET_ME = gql`
  {
    me {
      id,
      houses {
        id, name, description
      }
    }
  }
`

class AppContainer extends Component {

  state = {
    isAuthenticated: false,
    house: null,
    mqttClient: null
  }

  loginHandler = async (client) => {
    const { isAuthenticated } = this.state

    try {
      const result = await client.query({
        query: GET_ME
      })

      this.setState({
        isAuthenticated: true,
        house: result.data.me.houses[0],
        mqttClient: mqtt.connect(MQTT_URI)
      })
    } catch (e) {
      if (this._isMounted && isAuthenticated) {
        this.setState({isAuthenticated: false})
      }
    }
  }

  logoutHandler = (client) => {
    client.resetStore().then(() => {
      this.state.mqttClient.end()
      localStorage.removeItem(AUTH_TOKEN_KEY)
      this.setState({isAuthenticated: false})
    })
  }

  componentDidMount() {
    this._isMounted = true
  }

  componentWillUnmount = () => {
    this._isMounted = false
  }

  renderUnauthorized = (client) => {
    return <IdentificationPage loginHandler={this.loginHandler.bind(this, client)} />
  }

  render() {
    const { isAuthenticated, house, mqttClient } = this.state

    return (
      <ApolloConsumer>
        {client => {
          if (!isAuthenticated) {
            this.loginHandler(client)
            return this.renderUnauthorized(client)
          }

          return (
            <AuthContext.Provider value={{isAuthenticated, logoutHandler: this.logoutHandler.bind(this, client)}}>
              <MqttClientContext.Provider value={{client: mqttClient}}>
                <HouseContext.Provider value={{house}}>
                  {this.props.children}
                </HouseContext.Provider>
              </MqttClientContext.Provider>
            </AuthContext.Provider>
          )
        }}
      </ApolloConsumer>
    )
  }
}

export default AppContainer



