import React from 'react'
import {
  Title,
  Box,
  Media,
  MediaLeft,
  Icon,
  MediaContent,
  Subtitle,
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownContent,
  DropdownItem
} from 'bloomer'
import Toggle from 'components/Toggle'
import { MqttClientContext } from 'contexts'
import * as speech from 'speech'

class Module extends React.Component {
  state = {
    status: undefined,
    loading: false,
    subscribed: false,
    online: false
  }

  commitCommand = (command) => {
    const {status} = this.state
    const {module, mqttClient} = this.props
    this.setState({loading: true})

    const topic = `m/${module.identifier}/${command}`
    mqttClient.publish(topic, '1', null, (err) => {
      if (err) {
        // handle error
        return
      }

      this.setState({
        status: !status,
        loading: false
      })
    })
  }

  handleChange = (evt) => {
    const {status} = this.state
    const command = status ? '102' : '101'
    this.commitCommand(command)
  }

  handleVoiceCommand = (evt) => {
    this.commitCommand(evt.detail)
  }

  handleOnlineMessage = (topic, message) => {
    const { module } = this.props
    const [prefix, identifier] = topic.split('/')
    if (prefix !== 'm') return

    if (module.identifier === identifier) {
      const { online } = this.state
      const newOnline = message.toString() === '1'

      if (online !== newOnline) {
        this.setState({online: newOnline})
      }
    }
  }

  handleInformationMessage = (topic, message) => {
    const { module, room } = this.props
    const [prefix, identifier, type, informationId] = topic.split('/')

    if (prefix !== 'm') return

    if (module.identifier === identifier && type === '1' && informationId === '1') {
      const { status } = this.state
      const newStatus = message.toString() === '1'

      if (status !== newStatus) {
        if (status !== undefined) {
          speech.speak(`O mdulo ${module.name} no cmodo ${room.name} agora est ${newStatus ? 'ligado' : 'desligado'}!`)
        }
        this.setState({status: newStatus})
      }
    }
  }

  handleMessage = (topic, message) => {
    console.log(topic, message.toString())

    const topicParts = topic.split('/').length
    if (topicParts === 2) {
      this.handleOnlineMessage(topic, message)
    } else if (topicParts === 3) {
      // command
    } else if (topicParts === 4) {
      this.handleInformationMessage(topic, message)
    }
  }

  subscribe = () => {
    if (this.state.subscribed) return

    const { module, mqttClient } = this.props

    this.setState({
      subscribed: true
    }, () => {
      mqttClient.subscribe(`m/${module.identifier}/#`, null, (err) => {
        if (err) {
          this.setState({subscribed: false})
        } else {
          mqttClient.on('message', this.handleMessage)
        }
      })
    })
  }

  unsubscribe = () => {
    const { module, mqttClient } = this.props
    mqttClient.unsubscribe(`m/${module.identifier}/#`, () => {
      mqttClient.removeListener('message', this.handleMessage)
    })
  }

  componentDidMount() {
    this.subscribe()
    const {module} = this.props
    document.addEventListener(`command:${module.identifier}`, this.handleVoiceCommand)
  }

  componentWillUnmount() {
    this.unsubscribe()
    const {module} = this.props
    document.removeEventListener(`command:${module.identifier}`, this.handleVoiceCommand)
  }

  render() {
    const { module } = this.props
    const { status, loading, subscribed, online } = this.state

    let description = status ? 'ligado' : 'desligado'

    if (loading || !subscribed) {
      description = 'carregando'
    }

    if (!online) {
      description = 'desconectado'
    }

    return (
      <Box style={{opacity: online ? 1 : 0.25}} title={!online ? `${module.name} est desconectado.` : ''}>
        <Media>
          <MediaLeft>
            <Icon isSize="large" className={`fa fa-${module.moduleModel.icon} fa-3x`} style={{opacity: status ? 1 : .25, color: status ? module.moduleModel.color : 'inherit'}} />
          </MediaLeft>
          <MediaContent>
            <div>
              <Dropdown isPulled="right">
                <DropdownTrigger>
                  {/* <Button isOutlined aria-haspopup="true" aria-controls="dropdown-menu"> */}
                    <Icon isSize="small" icon="ellipsis-v" />
                  {/* </Button> */}
                </DropdownTrigger>
                <DropdownMenu>
                  <DropdownContent>
                    <DropdownItem href="#">Desativar</DropdownItem>
                  </DropdownContent>
                </DropdownMenu>
              </Dropdown>
              <Title isSize={5} style={{marginBottom: 10}}>{module.name}</Title>
              <Toggle isPulled="right" icons={false} checked={status || false} onChange={this.handleChange} disabled={loading || !online} />
              <Subtitle isPulled="left" isSize={6}>{description}</Subtitle>
            </div>
          </MediaContent>
        </Media>
      </Box>
    )
  }
}

export default (props) => (
  <MqttClientContext.Consumer>
    {({client}) => <Module mqttClient={client} {...props} />}
  </MqttClientContext.Consumer>
)



