import React, { Component } from 'react'
import { Modal, ModalBackground, ModalContent, Title, Subtitle, Box, Control, Input, Icon } from 'bloomer'
import { Mutation } from 'react-apollo'
import gql from 'graphql-tag';

const GET_MODULE_AVAILABLE = gql`
  mutation ($identifier: String!) {
    getModuleAvailable(identifier: $identifier) {
      module { id }
    }
  }
`

export default class ValidadeModule extends Component {
  state = {
    loading: false,
    identifier: ''
  }

  handleChange = async (getModuleAvailable, evt) => {
    const { validateHandler } = this.props
    const identifier = evt.target.value

    this.setState({identifier})

    if (!getModuleAvailable) {
      return
    }


    try {
      await getModuleAvailable({
        variables: { identifier }
      })
      this.setState({loading: true})
      setTimeout(() => validateHandler(true), 1500)
    } catch (err) {
      this.setState({loading: false})
    }
  }

  render() {
    const { loading, identifier } = this.state

    return (
      <Mutation mutation={GET_MODULE_AVAILABLE}>
      {(getModuleAvailable) => {
        return (
          <Modal isActive={true}>
            <ModalBackground />
            <ModalContent>
              <Box>
                <Title isSize={4}>Informe o identificador do mdulo adquirido</Title>
                <Subtitle isSize={6}>Veja as instrues no manual</Subtitle>

                <Control hasIcons="right">
                  <Input isSize="large" placeholder="Identificador" value={identifier} onChange={this.handleChange.bind(this, getModuleAvailable)} />
                  <Icon isAlign="right" isSize="large" className={`fa fa-${loading ? 'spinner fa-pulse' : 'barcode'} fa-3x`} />
                </Control>

              </Box>
            </ModalContent>
          </Modal>
        )
      }}
      </Mutation>
    )
  }
}



